import { useEffect, useRef } from "react";
import ReconnectingWebSocket from "reconnecting-websocket";

import { createAlertSocket, WebSocketMessageHandler } from "../services/websocket";

export const useWebSocket = (handler: WebSocketMessageHandler) => {
  const socketRef = useRef<ReconnectingWebSocket | null>(null);

  useEffect(() => {
    socketRef.current = createAlertSocket(handler);

    return () => {
      socketRef.current?.close();
    };
  }, [handler]);
};

