import ReconnectingWebSocket, { Options as ReconnectingWebSocketOptions } from "reconnecting-websocket";
import { WS_URL } from "../utils/constants";

export type WebSocketMessageHandler = (data: unknown) => void;

const defaultOptions: ReconnectingWebSocketOptions = {
  maxReconnectionDelay: 10000,
  minReconnectionDelay: 1000,
  reconnectionDelayGrowFactor: 1.3,
  connectionTimeout: 4000,
  maxRetries: Infinity
};

export const createAlertSocket = (onMessage: WebSocketMessageHandler, url = WS_URL) => {
  const socket = new ReconnectingWebSocket(url, [], defaultOptions);

  socket.addEventListener("message", (event) => {
    try {
      const payload = JSON.parse(event.data);
      onMessage(payload);
    } catch (error) {
      // eslint-disable-next-line no-console
      console.error("Failed to parse websocket payload", error);
    }
  });

  return socket;
};

