import { useCallback, useEffect, useMemo, useState } from "react";
import {
  Stack,
  Typography,
  Chip,
  ToggleButtonGroup,
  ToggleButton,
  Divider,
  SnackbarContent,
  Paper
} from "@mui/material";
import { useSnackbar } from "notistack";

import AlertCard from "../components/cards/AlertCard";
import LoadingSpinner from "../components/common/LoadingSpinner";
import { useSecurityDashboard } from "../hooks/useSecurityData";
import { useWebSocket } from "../hooks/useWebSocket";
import { AlertEvent } from "../types/security";

const severityLabel: Record<AlertEvent["severity"], string> = {
  critical: "critique",
  high: "élevée",
  medium: "modérée",
  low: "faible"
};

const AlertsPage = () => {
  const { data: dashboard, isLoading } = useSecurityDashboard();
  const [filter, setFilter] = useState<AlertEvent["severity"] | "all">("all");
  const [alerts, setAlerts] = useState<AlertEvent[]>([]);
  const { enqueueSnackbar } = useSnackbar();

  useEffect(() => {
    if (dashboard?.recentAlerts) {
      setAlerts(dashboard.recentAlerts);
    }
  }, [dashboard]);

  const handleIncomingAlert = useCallback(
    (payload: unknown) => {
      const incomingAlert = payload as AlertEvent;
      setAlerts((prev) => [incomingAlert, ...prev.slice(0, 49)]);
      enqueueSnackbar(`Alerte ${severityLabel[incomingAlert.severity]} : ${incomingAlert.title}`, {
        variant: incomingAlert.severity === "critical" ? "error" : "warning",
        content: (key, message) => (
          <SnackbarContent
            message={message}
            sx={{
              bgcolor: incomingAlert.severity === "critical" ? "error.dark" : "warning.dark",
              color: "common.white",
              fontWeight: 600
            }}
          />
        )
      });
    },
    [enqueueSnackbar]
  );

  useWebSocket(handleIncomingAlert);

  const filteredAlerts = useMemo(() => {
    if (filter === "all") return alerts;
    return alerts.filter((alert) => alert.severity === filter);
  }, [alerts, filter]);

  if (isLoading) {
    return <LoadingSpinner fullscreen message="Chargement des alertes…" />;
  }

  return (
    <Stack spacing={4}>
      <Stack direction="row" justifyContent="space-between" alignItems="center">
        <Stack spacing={1}>
          <Typography variant="h4" fontWeight={700}>
            Alertes
          </Typography>
          <Typography variant="body2" color="text.secondary">
            Liste des signalements reçus en temps réel.
          </Typography>
        </Stack>
        <Stack direction="row" alignItems="center" spacing={2}>
          <Chip color="error" label={`${alerts.length} au total`} />
          <ToggleButtonGroup
            size="small"
            value={filter}
            exclusive
            onChange={(_, value) => value && setFilter(value)}
          >
            <ToggleButton value="all">Toutes</ToggleButton>
            <ToggleButton value="critical">Critiques</ToggleButton>
            <ToggleButton value="high">Élevées</ToggleButton>
            <ToggleButton value="medium">Modérées</ToggleButton>
            <ToggleButton value="low">Faibles</ToggleButton>
          </ToggleButtonGroup>
        </Stack>
      </Stack>
      <Paper variant="outlined" sx={{ p: 3, borderRadius: 24, maxHeight: "70vh", overflowY: "auto" }}>
        <Stack spacing={2} divider={<Divider flexItem />}>
          {filteredAlerts.map((alert) => (
            <AlertCard key={alert.id} alert={alert} />
          ))}
        </Stack>
      </Paper>
    </Stack>
  );
};

export default AlertsPage;

